.scrollToTop {
    position: fixed;
    bottom: 0;
    right: 30px;
    width: 45px;
    height: 45px;
    background-color: var(--primary-color);
    border-radius: 5px;
    color: var(--bs-white);
    line-height: 45px;
    font-size: 20px;
    text-align: center;
    z-index: 9;
    cursor: pointer;
    transition: all 1s;
    transform: translateY(100%);
}

.scrollToTop i {
    color: var(--para-alt-color);
}

.scrollToTop.active {
    bottom: 30px;
    transform: translateY(0%);
}

.preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    z-index: 99999999;
    overflow: visible;
    background: url(../images/preloader.gif) no-repeat center center;
    background-size: 20%;
    background-color: #000;
}

.icon-svg {
    position: relative;
}

.icon-svg img {
    opacity: 1;
    position: absolute;
}

.icon-svg svg {
    position: absolute;
    stroke-dashoffset: 1500;
    stroke-dasharray: 1500;
    transform: scale(1);
    transition: transform 0.3s, fill 0.5s;
    opacity: 0;
    visibility: hidden;
}

.icon-svg:hover svg {
    visibility: visible;
    opacity: 1;
    stroke-dashoffset: 0;
    fill: transparent;
    transition: transform 0.3s, fill 0.5s, stroke-dashoffset 6s;
}

.icon-svg:hover img {
    opacity: 0;
    visibility: hidden;
}

.accordion-button:focus {
    box-shadow: none;
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both;
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translate3d(0, -100%, 0);
    }
    to {
        opacity: 1;
        transform: none;
    }
}

@keyframes item {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

.fadeInDown {
    animation-name: fadeInDown;
}

