    padding: 15px 35px;
    background-image: url(../images/btn-bg.png);
    background-size: 100% 112%;
    background-repeat: no-repeat;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: var(--para-alt-color);
}

.cmn-btn img,
.cmn-btn i {
    margin-left: 10px;
    font-size: inherit;
    color: var(--para-alt-color);
}

.cmn-btn:hover {
    color: var(--para-alt-color);
}

.cmn-btn:focus {
    color: var(--para-alt-color);
}

.cmn-btn.alt {
    background: transparent;
    border: 1px solid var(--secondary-color);
    color: var(--bs-white);
}

.cmn-btn.alt i {
    color: var(--bs-white);
}

.cmn-btn.alt:hover {
    background: var(--secondary-color);
    border: 1px solid transparent;
    color: var(--para-color);
}

.cmn-btn.alt:hover i {
    color: var(--para-white);
}

.nice-select {
    background-color: rgb(65, 34, 109);
    border: 1px solid transparent;
    float: initial;
    padding: 13px 20px;
    display: flex;
    align-items: center;
    line-height: initial;
    height: 100%;
    width: 100%;
}

.nice-select:focus,
.nice-select:hover {
    border-color: transparent;
}

.nice-select::after {
    height: 9px;
    width: 9px;
    right: 20px;
    top: initial;
}

.nice-select .list {
    width: 100%;
    background-color: rgb(76, 41, 126);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0;
    margin-bottom: 4px;
    padding: 0;
}

.nice-select .current {
