}

.nice-select .option:hover,
.nice-select .option:focus,
.nice-select .option.selected.focus {
    color: var(--primary-color);
    background-color: transparent;
}

.thumb-select .current {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 30px;
}

.thumb-select .current::before {
    content: "";
    position: absolute;
    background-image: url(../images/icon/btc-icon.png);
    background-size: auto;
    background-repeat: no-repeat;
    width: 18px;
    height: 18px;
    z-index: 1;
    left: 0;
}

.slick-arrow {
    position: absolute;
    outline: none;
    bottom: -70px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid rgb(121, 121, 168);
    background-color: rgba(49, 20, 104, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.slick-arrow i {
    color: var(--bs-white);
    font-size: 14px;
}

.slick-arrow:hover {
    border-color: var(--bs-white);
    transition: all 0.5s;
}

.slick-arrow.slick-prev {
    left: calc(50% - 100px);
    z-index: 2;
}

.slick-arrow.slick-next {
    right: calc(50% - 100px);
}

.section-dots {
    text-align: center;
    position: absolute;
    bottom: -62px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.section-dots li {
    margin: 0 5px;
    display: inline-block;
    list-style: none;
}

.section-dots li .dot {
    display: block;
    width: 14px;
    height: 14px;
    border-radius: 10px;
    background: transparent;
    border: 1px solid rgba(156, 167, 255, 0.4);
    text-decoration: none;
    cursor: pointer;
    transition: all ease 0.3s;
}

.section-dots li .dot .string {
    position: absolute;
    line-height: 0;
    opacity: 0;
}

.section-dots li.slick-active .dot {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
}

.input-area {
    border-radius: 5px;
    background-color: var(--bs-white);
    padding: 0 5px;
    margin: 30px 0 20px;
}

.input-area:focus-within {
    outline: 1px solid var(--primary-color);
    border: none;
}

.input-area input {
    color: var(--para-color);
    padding: 10px;
    border: none;
    background: transparent;
}

.input-area button {
    padding: 4px 10px;
    background-color: transparent;
}

.single-radio {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    display: grid;
    align-items: center;
    margin-bottom: 10px;
    padding: 16px 40px;
}

.single-radio:hover input~.checkmark {
    background-color: transparent;
}

.single-radio .checkmark {
    position: absolute;
    left: 0;
    width: 28px;
    height: 28px;
    background-color: transparent;
    border: 1px solid var(--bs-white);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.single-radio .checkmark::after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    background-color: transparent;
}

.single-radio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

.single-radio input:checked~.checkmark {
    background-color: transparent;
    border: 1px solid rgb(240, 173, 78);
}

.single-radio input:checked~.checkmark::after {
    display: block;
    background-color: rgb(240, 173, 78);
}

.checkbox-single {
    position: relative;
    padding-left: 35px;
    margin-bottom: 15px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
}

.checkbox-single:last-child {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
}

.checkbox-single .content-area {
    display: flex;
}

.checkbox-single .checkbox-area {
    align-items: center;
}

.checkbox-single .checkbox-area input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkbox-single .checkbox-area .checkmark {
    position: absolute;
    left: 0;
    top: 3px;
    height: 24px;
    width: 24px;
    border-radius: 5px;
    background-color: rgb(102, 53, 168);
}

.checkbox-single .checkbox-area .checkmark::after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox-single .checkmark {
    display: flex;
    align-items: center;
    justify-content: center;
}

.checkbox-single .checkmark::after {
    width: 14px;
    height: 12px;
    background-image: url(../images/icon/check.png);
    background-repeat: no-repeat;
}

.checkbox-single input:checked~.checkmark {
    border-color: transparent;
}

.checkbox-single input:checked~.checkmark:after {
    display: block;
}

.checkbox-single u {
    text-decoration: underline;
}

.checkbox-single u a {
    text-decoration: inherit;
    color: inherit;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes ripple2Ani {
    0% {
        transform: translate(0px, 0px);
    }
    33% {
        transform: translate(-5px, -5px);
    }
    66% {
        transform: translate(5px, 5px);
    }
    100% {
        transform: translate(0px, 0px);
    }
}

@keyframes stretch {
    0% {
        transform: scale(0.9);
    }
    50% {
        transform: scale(0.7);
    }
    100% {
        transform: scale(1);
    }
    90% {
        transform: scale(0.8);
    }
    50% {
        transform: scale(0.7);
    }
    0% {
        transform: scale(0.9);
    }
}

@keyframes rippleAni {
    0% {
        transform: translate(0px, 0px);
    }
    33% {
        transform: translate(5px, -5px);
    }
    66% {
        transform: translate(-5px, 5px);
    }
    100% {
        transform: translate(0px, 0px);
    }
}

